<template>
  <div class="">
    <header class="header">
      <div class="header-cont">
        <div class="header-left">
          <i><img src="../assets/img/PC-logo-top.png" alt=""></i>
          <span>中商会财税管理后台</span>
        </div>
        <div class="header-right">
          <a href="javascript:;" @click="showDialogPwd=true">修改密码</a>
          <span>|</span>
          <a href="javascript:;" @click="exit">退出</a>
        </div>
      </div>

      <el-dialog title="修改密码" :visible.sync="showDialogPwd" class="digcont">
        <el-form :model="form" ref="form" :rules="rules">
          <el-form-item label="密码" prop="user_password" :label-width="formLabelWidth">
            <el-input v-model="form.user_password" type="password" class="diainp" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="user_new_password" :label-width="formLabelWidth">
            <el-input v-model="form.user_new_password" type="password" class="diainp" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="user_validate_password" :label-width="formLabelWidth">
            <el-input v-model="form.user_validate_password" type="password" class="diainp" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="showDialogPwd = false">取 消</el-button>
          <el-button type="primary" @click="saveChange('form')">确 定</el-button>
        </div>
      </el-dialog>

    </header>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      showDialogPwd: false,
      form:{
        user_account:localStorage.getItem('user'),
        user_password:'',
        user_new_password:'',
        user_validate_password:''
      },
      formLabelWidth: '120px',
      rules: {
        user_account:[
          {required: true, message: '请输入账号', trigger: 'blur'}
        ],
        user_password:[
          {required: true, message: '请输入密码', trigger: 'blur'},
          {validator:this.validateSpace,trigger:'blur'},
          {min:3,max:32,message:'长度在3到32个字符',trigger:'blur'}
        ],
        user_new_password:[
          {required: true, message: '请输入新密码', trigger: 'blur'},
          {validator:this.validateSpace,trigger:'blur'},
          {min:3,max:32,message:'长度在3到32个字符',trigger:'blur'}
        ],
        user_validate_password:[
          {required: true, message: '请输入确认密码', trigger: 'blur'},
          {validator:this.validateRepwd,trigger:'blur'}
        ]
      },


    }
  },
  methods:{
    saveChange: function(formName){
      var self = this;
      self.$refs[formName].validate(function(valid){
        if(valid){
          var params = {
            username: self.form.user_account,
            old_password:self.form.user_password,
            new_password: self.form.user_new_password
          };
          self.$axios.put(self.baseUrl+'/api/v1/admin/password',params).then(function(res){
            if(res.data.success){
              self.showDialogPwd = false;
              self.$message({message:'修改成功，请重新登录',type:'success'});
              setTimeout(function(){
                self.$router.push('/login');
              },2000)
            }else{
              self.$message.error(res.data.msg);
            }
          },function(err){
//                    self.loading = false;
            self.$message.error(err);
          })
        }else {
          console.log('error submit!!');
          return false;
        }
      })


    },
    exit(){
      localStorage.clear();
      this.$router.push('/login');
    },
    validateRepwd: function(rule,value,callback){
      if(value !== this.form.user_new_password){
        callback(new Error('两次输入密码不一致'));
      }else{
        callback();
      }
    },


  }
}
</script>
<style scoped>
  .diainp{width:217px;}
  .header-left{
    float: left;
  }
  .header-right{
    float: right;
    font-size:14px;
    color:#fff;
  }
  .header-right span{
    font-size:12px;
    color:#999;
  }
  .header-right a{
    display: inline-block;
    padding:0 15px;
    color:#aaa;
  }
  .header{
    height:80px;
    color:#fff;
    background-color:#242f42;
  }
  .header-cont{
    width:100%;
    height:100%;
    line-height: 80px;
    margin:0 auto;
    text-align: left;
    padding:0 20px;
    overflow: hidden;
  }
  .header-cont i{
    width:58px;
    height:59px;
    display: inline-block;
    margin:0;
    padding:0;
    vertical-align: middle;
  }
  .header-cont span{
    display: inline-block;
    vertical-align: middle;
    padding-left:10px;
    font-size: 22px;
    letter-spacing: 2.03px;
  }
</style>



