import  ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

Vue.config.productionTip = false

/* eslint-disable no-new */
Vue.use(ElementUI)
//Vue.prototype.$axios = axios;
// Vue.prototype.baseUrl = 'http://192.168.19.2:8000'
Vue.prototype.baseUrl = 'https://tax.caishuidai.com'
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'

})



