import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/Login'
//import Home from '@/pages/Home'

Vue.use(Router)

export default new Router({
  mode:'history',
  base:'admin',
  routes: [
    {
      path:'/',
      redirect:'/login'
    },
    {
      path: '/login',
      name: 'Login',
      // component: Login
      component:resolve => require(['../pages/Login.vue'],resolve)
    },
    {
      path:'/home',
      name:'Home',
      component:resolve => require(['../pages/Home.vue'],resolve),
      children:[
        {
          path:'/first',
          component:resolve => require(['../pages/First.vue'],resolve)
        },
        {
          path:'/certificate',
          component:resolve => require(['../pages/Cert.vue'],resolve)
        },
        {
          path:'/edit',
          component:resolve => require(['../pages/Edit.vue'],resolve)
        },
        {
          path:'/add',
          component:resolve => require(['../pages/Add.vue'],resolve)
        },
        // {
        //   path:'/certsmanage',
        //   component:resolve => require(['../pages/CertsManage.vue'],resolve)
        // }

      ]
    }
  ]
})



