<template>
  <div class="wrap">
    <header class="login-header">
      <div class="header-cont">
        <i><img src="../assets/img/PC-logo-top.png" alt=""></i>
        <span>中商会财税管理后台</span>
      </div>
    </header>
    <main class="login-main">
      <div class="main-left">
        <div class="">
          <div class="main-logo">
            <i><img src="../assets/img/PC-logo.png" alt=""></i>
          </div>
          <div class="main-form">
            <div class="form-item">
              <span class="login-label">账号</span>
              <input v-model="user" class="login-inp" type="text">
            </div>
            <div class="form-item">
              <span class="login-label">密码</span>
              <input v-model="pwd" class="login-inp" type="password"  @keyup="clickKeyCode($event)">
            </div>
            <div class="form-item">
              <span class="login-label"></span>
              <input class="login-inp login-btn" type="button" @click="submit" value="提 交">
            </div>
          </div>
        </div>
      </div>
      <div class="main-right">
        <i><img src="../assets/img/PC-big.png" alt=""></i>
      </div>
    </main>
    <footer></footer>
  </div>
</template>

<script>
  import vHead from '../components/Header'
export default {
  name: 'Login',
  components: {
   vHead
  },
  data () {
    return {
      user:'',
      pwd:''
    }
  },
  created(){
    //console.log(this.baseUrl);
  },
  methods:{
    clickKeyCode:function(ev){
      if(ev.keyCode == 13){
        this.submit();
      }
    },
    submit: function(){
      var self = this;
      if(self.user == ''){
        self.$message({message:'请输入账号',type:'warning'});
        return false;
      }
      if(self.pwd == ''){
        self.$message({message:'请输入密码',type:'warning'});
        return false;
      }
      self.$axios.get(self.baseUrl+'/api/admin/login',{
        params:{
          username:self.user,
          password:self.pwd
        }
      }).then(function(res){
        if(res.data.success){
          localStorage.user = self.user;
          //self.$router.push('/certificate?cert_id=0');
          self.$router.push('/first');
        }else{
          self.$message.error(res.data.msg);
        }
      })
    },
  }
}
</script>

<style scoped>
.wrap{
  width:100%;
  height:100%;
  /*background-color:#f6f6f6;*/
}
  .login-header{
    min-width:1000px;
    background-color:#fff;
    height:80px;
    box-shadow: 0 2px 7px 0 rgba(216,216,216,0.50);
  }
  .header-cont{
    width:1000px;
    height:100%;
    line-height: 80px;
    margin:0 auto;
    text-align: left;
  }
  .header-cont i{
    width:58px;
    height:59px;
    display: inline-block;
    margin:0;
    padding:0;
    vertical-align: middle;
  }
  .header-cont span{
    display: inline-block;
    vertical-align: middle;
    padding-left:10px;
    font-size: 22px;
    color: #333333;
    letter-spacing: 2.03px;
  }

  .login-main{
    width:1000px;
    /*height:600px;*/
    overflow: hidden;
    margin:20px auto 35px;
    padding-bottom:40px;
    background-color:#fff;
    border-top:2px solid #D19421;
    font-size:18px;
    color:#333;
  }
  .main-left{
    width:540px;
    margin:40px 0;
    float: left;
  }
  .main-right{
    width:450px;
    margin:40px 0 0;
    padding:20px 0;
    float:right;
    border-left:1px solid #ddd;
    text-align: center;
  }
  .main-logo{
    margin:40px auto;
    width:97px;
    height:98px;
  }
  .form-item{
    width:400px;
    margin:0 auto 20px;
  }
  .login-label{
    display: inline-block;
    width:60px;
    height:46px;
    line-height: 46px;
    text-align: right;
  }
  .login-inp{
    width:300px;
    height:46px;
    line-height:46px;
    box-sizing: border-box;
    border:1px solid #ccc;
    padding-left:16px;
    margin-left:10px;
    border-radius: 3px;
    outline: none;
  }
  .login-inp.login-btn{
    padding:0;
    border:none;
    background-color:#D19421;
    color:#fff;
    font-size:18px;
    cursor: pointer;
  }
  .login-btn:hover{
    background-color:#E6A529;
  }
  .main-right i{
    display: inline-block;
    width:300px;
    height:435px;
  }
  .main-right i img{
    width:100%;
    height:100%;
  }
</style>



