import Vue from 'vue'
import vuex from 'vuex'
import axios from 'axios'

//import AxiosPlugin from '../assets/js/AxiosPlugin'
//Vue.use(AxiosPlugin)

Vue.use(vuex);

export default new vuex.Store({
  state: {
    siderbarItems: [
      {
        icon: 'el-icon-document',
        index: 'first',
        title: '首页'
      },
      {
        icon: 'el-icon-menu',
        index: 'zslb',
        title: '证书列表',
        subs: [

        ]
      },
      {
        icon: 'el-icon-setting',
        index: 'zsgl',
        title: '证书管理',
        subs: [
          // {
          //   index: 'certsmanage',
          //   title: '证书管理'
          // },
          {
            index: 'edit',
            title: '编辑证书'
          },
          {
            index: 'add',
            title: '添加证书'
          }
        ]

      }
      ],
    defaultSider : '',
    token:''

  },
  mutations:{
    setSiderbarItems(state,data){
      state.siderbarItems = data;
      state.defaultSider = data[1].subs[0].index.split('=')[1];
    },
    setToken(state,token){
      state.token = token;
      localStorage.token = token
    },

  },
  actions:{
    fetchSiderbarItems(context){

    },
    loadSiderbarData(store){
      let token = localStorage.token;
      // axios.get('https://tax.caishuidai.com'+'/api/v1/admin/certs').then(res =>{
      axios({
        type:'get',
        // url:'http://192.168.19.2:8000'+'/api/v1/admin/certs',
        url:'https://tax.caishuidai.com'+'/api/v1/admin/certs',
        headers:{Authorization:'Bearer '+token}

      }).then(res =>{
        //console.log(res.data.data);
        var oldsider = store.state.siderbarItems;
        var oldarr = res.data.data;
        let arr = [];
        for(var i=0;i<oldarr.length;i++){
          let newitem = {
            index:'certificate?cert_id='+oldarr[i].cert_id,
            title:''+ oldarr[i].cert_name
          }
          arr.push(newitem)
        }
        oldsider[1].subs = arr;
        //console.log(oldsider);
        store.commit('setSiderbarItems', oldsider)

      })


    }

  }


})



